@use '../base/variables' as v;
@use '../base/mixins' as m;

.paquetes {
    &__grid {
        @include m.contenedor;
        @include m.grid(1, 3rem);

        @include m.tablet {
            @include m.grid(3, 3rem);
            align-items: flex-start;
        }

    }


}

.paquete {
    @include m.card;

    &__lista {
        @include m.reset-lista;
        line-height: 2;
    }

    &__precio {
        font-size: 6rem;
        margin: 3rem 0;
        color: v.$azul;
        font-weight: v.$black;
        text-align: center;
    }

    &:nth-child(2) {
        order: -1;

        @include m.tablet {
            order: unset;
        }
    }

    &__submit {
        @include m.boton(v.$azul, v.$blanco);
        margin-top: 3rem;
    }

}