@use 'base/variables' as v;
@use 'base/mixins' as m;

.horas {
    @include m.reset-lista;
    @include m.grid(1, 1rem);
    margin-bottom: 1rem;

    ul {
        @include m.tablet {
            @include m.grid(2, 1rem);
        }
    }

    &__hora {

        color: v.$azul;
        border: 1px solid v.$azul;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;

        transition-property: scale;
        transition-duration: 300ms;
        &:hover {
            scale: 1.02;
            cursor: pointer;
        }

        &--seleccionada {
            @extend .horas__hora;
            color: v.$blanco;
            background-color: v.$azul;
        }

        &--deshabilitado {
            @extend .horas__hora;
            border: 1px solid v.$gris;
            color: v.$gris;
            opacity: .5;

            &:hover{
                cursor: not-allowed;
            }
        }

    }
}